@tailwind base;
@tailwind components;
@tailwind utilities;

*{
  box-sizing: border-box;
  text-decoration: none;
  margin:0;
  padding:0;
  list-style: none;
  //border:solid 1px red;
}

:root {
  --bianco: #fbfbfd;
  --grafite: #111111;
  --grafitechiaro:#1a1a1a;
  --azzurro: #1561f0;
  --grigiochiaro:#6e6e73;
  --grigioscuro:#1d1d1f;
  --azzurro-alpha: #1561f088;

  --rosso:#ff0000;
  --rosso-alpha:#ff000022;
  --verde:#16ff00;
}

body {

}

.game-board{
  min-height:100vh;
  background:var(--grafitechiaro);
  color:var(--bianco);
}
.info-wrapper{
  display:flex;
  flex-direction:column;
}
.info-wrapper button {
  background: var(--bianco);
  color: var(--grafite);
  border: 1px solid var(--grafite);
  border-radius:10px;
  outline: none;
  cursor: pointer;
  padding: 0.5rem;
  margin: 0.5rem 1rem;
}
.board {
  margin:0 auto 0 auto;
  border: 1em solid var(--grafite);
  box-shadow: 0px 0px 10px var(--grafite);
  background: var(--grafite);
  width:90%;
  aspect-ratio: 1 / 1; 
  max-width: 500px;
  max-height: 500px;
  //width: 500px;
  //height: 500px;
  display: grid;
  grid-template: repeat(3, 1fr) / repeat(3, 1fr);
  gap: 5%;
  border-radius:10px;
}

.squares {
  
  
  background: var(--grigioscuro);
  border: none;
  border-radius:10px;
  font-size: 6em;
  aspect-ratio: 1 / 1; 
  /* font-weight: 800; */
  cursor: pointer;
  outline: none;
  display: inline-flex;
  align-items: center;
  justify-content:center;
  padding-bottom:0.1em;
}

.X {
  color: var(--rosso);
  box-shadow: 0px 0px 10px var(--rosso);
}
.O {
  color: var(--verde);
  box-shadow: 0px 0px 10px var(--verde);
}
h1{
  font-size:2em;
  padding:0.2em;
  text-align:center;
}

h3{
  font-size:1.5em;
  padding:0.5em;
  //text-align:center;
}
.stats-bar{
  display:flex;
  justify-content:space-between;
  max-width:500px;
  margin:auto;
}